body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{z-index:50;display:flex;position:relative;left:0;justify-content:center;flex-direction:row;background-color:#66FCF1}nav.variant-home{background-color:unset}.navbar-group{display:flex;flex-direction:column;text-align:center;text-transform:uppercase;width:300px}.nav-divider-wrapper{height:22px;width:10px;display:flex;align-items:center}@media screen and (min-width: 320px){.nav-divider-wrapper{height:calc(22px + (60 - 22) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){.nav-divider-wrapper{height:60px}}.nav-divider-wrapper .nav-divider{width:80%;padding-top:80%;border-radius:100%;background-color:black}.link{font-family:'PT Serif', serif;display:block;transition:ease .2s transform}.link:hover{color:blue;transform:scale(1.2)}.link-h1{font-size:15px;color:#1F2833}@media screen and (min-width: 320px){.link-h1{font-size:calc(15px + (50 - 15) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){.link-h1{font-size:50px}}.link-h2{font-size:8px;padding:1% 0;color:#1F2833;text-transform:none;text-transform:initial}@media screen and (min-width: 320px){.link-h2{font-size:calc(8px + (20 - 8) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){.link-h2{font-size:20px}}

main{position:relative;background-color:#1F2833}.container{z-index:20;display:flex;flex-direction:column;justify-content:center;position:relative;min-height:100vh;height:100%;padding:0;box-shadow:black 0px 0px 700px;background-color:white;border:4px solid black;border-top:0px;border-bottom:0px}@media only screen and (max-width: 425px){.container{padding-left:0}nav{position:-webkit-sticky;position:sticky;height:10vh;width:100%;top:0}}.jumbo{font-size:20px;font-family:'Permanent Marker', sans-serif;color:#66FCF1;font-weight:100;letter-spacing:0.2em;text-align:center}@media screen and (min-width: 320px){.jumbo{font-size:calc(20px + (100 - 20) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){.jumbo{font-size:100px}}

.main-fx{position:absolute;content:'';height:100%;width:100%;background-repeat:repeat}.main-fx::before,.main-fx::after{position:absolute;top:0;left:0;width:100%;height:100%}#main-fx-1{z-index:2;background-size:500%;opacity:.75;-webkit-filter:blur(10px) sepia(1);filter:blur(10px) sepia(1)}#main-fx-2{z-index:1;opacity:0.3;-webkit-filter:blur(3px);filter:blur(3px);background:url(/tyler-smith-portfolio/static/media/paint-splatter-tiled.52be1229.png)}

#home-title-container{order:-1;justify-self:center;height:100%;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;margin:0% 0% 25px 0%}.home-title{font-size:40}@media screen and (min-width: 320px){.home-title{font-size:calc(40px + (200 - 40) * ((100vw - 320px) / (1450 - 320)))}}@media screen and (min-width: 1450px){.home-title{font-size:200px}}.fxtext__text{text-shadow:black 0px 5px 10px, black 5px 0px 10px, black -5px 0px 10px}

.fxtext{position:relative}.fxtext .fxtext__text{position:relative}.fxtext .fxtext__overlay{top:0;left:0;right:0;bottom:0;position:absolute;z-index:2;color:transparent;background:url(/tyler-smith-portfolio/static/media/paint-splatter-tiled.52be1229.png);background-clip:text;-webkit-background-clip:text;-moz-background-clip:text;background-position-y:50%;background-repeat:repeat;background-attachment:local;background-size:1000px;-webkit-filter:invert(80%);filter:invert(80%)}

.section-heading{font-size:15px;font-family:'PT Serif', serif;text-align:center;color:#66FCF1}@media screen and (min-width: 320px){.section-heading{font-size:calc(15px + (40 - 15) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){.section-heading{font-size:40px}}.section-heading hr{border-color:#66FCF1;border-width:2px;border-style:solid;margin-top:10px;width:10vw}

.project{width:46%;margin:2% 2%;text-align:center}.project .project-img{background-color:#0B0C10;width:20%;border-radius:100%}.project .project-title{font-family:'Staatliches', sans-serif;font-size:20px;color:#C5C6C7;border-bottom:1px gainsboro solid}@media screen and (min-width: 320px){.project .project-title{font-size:calc(20px + (40 - 20) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){.project .project-title{font-size:40px}}

#project-container{display:flex;flex-direction:row;flex-wrap:wrap}

#construction-container{height:100%;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}section{display:flex;flex-direction:column;justify-content:flex-start;align-items:center;width:75%}.fxtext{margin-bottom:10%}.fxtext__overlay,.fxtext__text{width:100%;font-size:20px}@media screen and (min-width: 320px){.fxtext__overlay,.fxtext__text{font-size:calc(20px + (80 - 20) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){.fxtext__overlay,.fxtext__text{font-size:80px}}h1{font-size:20px;opacity:1;font-style:italic;text-align:center}@media screen and (min-width: 320px){h1{font-size:calc(20px + (60 - 20) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){h1{font-size:60px}}h2{font-size:40px;text-align:center}@media screen and (min-width: 320px){h2{font-size:calc(40px + (90 - 40) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){h2{font-size:90px}}p{font-size:16px;text-align:center}@media screen and (min-width: 320px){p{font-size:calc(16px + (20 - 16) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){p{font-size:20px}}a{font-size:20px}@media screen and (min-width: 320px){a{font-size:calc(20px + (24 - 20) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){a{font-size:24px}}span{width:50%;display:flex;justify-content:space-evenly}@media screen and (max-width: 770px){span{width:90%}}hr{width:50%;margin:4% 0}small{width:50%;font-size:12px}@media screen and (min-width: 320px){small{font-size:calc(12px + (14 - 12) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){small{font-size:14px}}small a{font-size:inherit}

#underconstruction{font-size:20px;font-family:'Allerta Stencil', sans-serif;position:fixed;top:0;left:0;width:100%;box-shadow:0px 2px 7px 1px black;border-left:none;border-right:none;z-index:100;text-align:center;font-weight:bolder;background-color:#ffe600}@media screen and (min-width: 320px){#underconstruction{font-size:calc(20px + (60 - 20) * ((100vw - 320px) / (1000 - 320)))}}@media screen and (min-width: 1000px){#underconstruction{font-size:60px}}

